.item {
    display: flex;
    align-items: center;

    margin: 0 auto 10px;
    width: 300px;
    height: 80px;
    background-color: rgb(247, 249, 242);
    border-radius: 8px;
    box-shadow: 0px 5px 10px 0px rgba(29, 29, 29, 0.5);
}

.item:hover {
    scale: 1.1;
}

.item:last-child {
    margin-bottom: 30px;
}

.isOnline,
.offline {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    margin-left: 20px;
    background-color: rgb(8, 178, 5);
}

.offline {
    background-color: rgb(218, 5, 5);
}

.avatar {
    margin-left: 15px;
}

.name {
    margin-left: 10px;
    font-weight: 500;
    font-size: 16px;
    line-height: 2.14;
}